import React from 'react'; 
import Icon  from 'react-ionicons'; 

const CardLoading = props => {
    return (
        <div className="pages">
        <Icon icon="ios-refresh" fontSize="80px" color="#347eff" rotate={true} />
        </div>
    ); 
}

export default CardLoading; 



